import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CreateComponent } from './pages/create/create.component';
import { ExternalComponent } from './pages/external/external.component';
import { HomeComponent } from './pages/home/home.component';
import { ListsComponent } from './pages/lists/lists.component';
import { MessagesComponent } from './pages/messages/messages.component';
import { MyAccountComponent } from './pages/my-account/my-account.component';
import { UsersComponent } from './pages/users/users.component';

const routes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'home/about', component: HomeComponent },
  { path: 'home/service', component: HomeComponent },
  { path: 'home/documentation', component: HomeComponent },
  { path: 'home/contact', component: HomeComponent },
  { path: 'users', component: UsersComponent },
  { path: 'lists', component: ListsComponent },
  { path: 'external', component: ExternalComponent },
  { path: 'messages', component: MessagesComponent },
  { path: 'my-account', component: MyAccountComponent },
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  // { path: '**', component: NotFoundComponent }
 ];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
