import { AfterContentChecked, AfterContentInit, DoCheck } from '@angular/core';
import { AfterViewInit } from '@angular/core';
import { AfterViewChecked } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { TreeNode } from 'primeng/api/treenode';
import { User } from 'src/app/common/data/user';
import { DataService, MessageData } from 'src/app/common/service/data.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements AfterContentChecked {

  public menuHidden: boolean = true;

  users: any[] = [];
  cols: any[] = [];
  filesTree: TreeNode[] = [];

  constructor(private dataService: DataService, private route: ActivatedRoute) { 

  } 

  ngAfterViewInit  () {
    console.log('into');
    this.route.fragment.subscribe ( f => {
        const element = document.querySelector("#" + f);
        if ( element ) element.scrollIntoView ({behavior:"smooth"});
    }); 
  }
  ngAfterContentChecked  () {
    this.filesTree = this.dataService.getTree();
  }

  public showTableData(data: any) {
    this.users = data['users'];
    this.cols = data['cols'];
  }

  public nodeSelect(event:any): void {
    let obj:MessageData = {};
    obj[event.node.children ? "message" : "color"] = event.node.key;
    this.dataService.upd(obj);
  }

  
  public onReset() {
    console.log('>>>>>');
  }
}