import { Inject } from '@angular/core';
import { Input } from '@angular/core';
import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ConfirmationService } from 'primeng/api';
import { BehaviorSubject } from 'rxjs';
import { DataService } from 'src/app/common/service/data.service';

@Component({
  selector: 'app-all-categories',
  templateUrl: './all-categories.component.html',
  styleUrls: ['./all-categories.component.scss']
})
export class AllCategoriesComponent {

  @Output() onDateSearch = new EventEmitter<any>();
  
  countries = ['United States', 'England', 'Bolivia'];
  years = Array.from(Array(20).keys()).map(i => 1970 + i);

  dataService: DataService;
  formAll: FormGroup;

  constructor(dataService:DataService, formBuilder: FormBuilder) {
    this.formAll = formBuilder.group({
      code: [null, Validators.required],
      reference: [null, [Validators.required]]
    });
    this.dataService = dataService;
  }

  public onSubmit(): void {
    let data = this.dataService.getData();
    this.onDateSearch.emit(data);
  }

  onReset() {
    this.formAll!.reset();
  }

}
