import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-vessel',
  templateUrl: './vessel.component.html',
  styleUrls: ['./vessel.component.scss']
})
export class VesselComponent {

  formvessel: FormGroup;

  constructor(private formBuilder: FormBuilder) {
    this.formvessel = this.formBuilder.group({
      name: [null, Validators.required],
      email: [null, [Validators.required, Validators.email]]
    });
  }

  onSubmit() {
    if (this.formvessel!.valid) {
      console.log('form submitted', this.formvessel.get('name')?.value);
    } else {
      // validate all form fields
    }
  }

  reset() {
    this.formvessel!.reset();
    // this.formSubmitAttempt = false;
  }

}
