import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { DataService } from 'src/app/common/service/data.service';

@Component({
  selector: 'app-top-main',
  templateUrl: './top-main.component.html',
  styleUrls: ['./top-main.component.scss']
})
export class TopMainComponent {

  subtitle:string = 'home';
  color:string = 'white';
  message:string = 'No messages to be printed right now';
  
  constructor(private translate:TranslateService, dataService:DataService) { 
    if(location.pathname.length > 1) {
      this.subtitle = location.pathname.substring(1);
    }
    this.subtitle = translate.instant(this.subtitle);
    dataService.updateObservable.subscribe(
      data => {
        this.color = data.color ? data.color : this.color;
        this.message = data.message ? data.message : this.message;
      }
    );
  }

}
